html{height:100%;font-family:'Orbitron', sans-serif;font-size:62.5%}body{margin:0;font-family:'Orbitron', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale;height:100%}.root{height:100%}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

.app{background-image:linear-gradient(black, #290036);min-height:100%;margin:0;padding:0;display:flex}

.menu{display:flex;flex-direction:column;align-items:center;justify-content:center;max-height:100%;width:100%;overflow:hidden}.menu__title{max-width:calc(100% - 20px);max-height:150px;transform:translateY(-100vh);-webkit-animation:0.75s forwards ease-out title;animation:0.75s forwards ease-out title}.menu__logo{max-width:calc(100% - 60px);max-height:calc(100vh - 280px);opacity:0;transform:scale(0.8);-webkit-animation:0.4s 0.75s forwards ease-out show;animation:0.4s 0.75s forwards ease-out show}.menu__controllers{display:flex;flex-flow:row wrap;justify-content:space-evenly;width:100%}.menu__controllers>*{transform:scale(0.8);opacity:0}.menu__controllers>*:nth-child(1){-webkit-animation:0.3s 1.2s forwards ease-out show;animation:0.3s 1.2s forwards ease-out show}.menu__controllers>*:nth-child(2){-webkit-animation:0.3s 1.5s forwards ease-out show;animation:0.3s 1.5s forwards ease-out show}@-webkit-keyframes title{from{transform:translateY(-100vh)}to{transform:translateY(0)}}@keyframes title{from{transform:translateY(-100vh)}to{transform:translateY(0)}}@-webkit-keyframes show{0%{opacity:0;transform:scale(0.8)}80%{opacity:1;transform:scale(1.2)}100%{opacity:1;transform:scale(1)}}@keyframes show{0%{opacity:0;transform:scale(0.8)}80%{opacity:1;transform:scale(1.2)}100%{opacity:1;transform:scale(1)}}

.custom-btn{border:none;padding:0.75rem 2rem;cursor:pointer;background-color:#11da2b;border-radius:10px;border:1px solid white;font-size:2.5rem;font-family:'Orbitron', sans-serif;font-weight:bold;margin:1rem;color:white;min-width:22rem}.custom-btn:hover,.custom-btn:active{background-color:#2dff49}.custom-btn--collapse{min-width:0}.custom-btn--secondary{background-color:#00AFC7}.custom-btn--secondary:hover,.custom-btn--secondary:active{background-color:#00cce7}.custom-btn--dead{background-color:#b10018}.custom-btn--dead:hover,.custom-btn--dead:active{background-color:#e62740}.custom-btn--menu:hover,.custom-btn--menu:active{box-shadow:0px 0px 25px 5px #9aff47}.custom-btn--menu.custom-btn--secondary:hover,.custom-btn--menu.custom-btn--secondary:active{box-shadow:0px 0px 25px 5px #00cce7}

.settings{display:flex;justify-content:space-around;align-items:center;flex-direction:column;width:100%;color:white}.settings .form{width:100%;max-width:600px}.settings .field{padding:1.8rem 1rem;width:calc(100% - 20px)}.settings .field__name{font-size:3rem;margin-left:1rem}.settings .field__input{border:1px solid rgba(255,255,255,0);background-color:rgba(255,255,255,0.349);font-family:'Orbitron', sans-serif;font-size:2.5rem;text-align:center;color:white;padding:0.5rem;border-radius:10px;width:calc( 100% - 20px)}.settings .field__input--error{border:1px solid red}.settings .field__error{color:red;margin-left:2rem;font-size:1.3rem;opacity:0}.settings .field__error--show{opacity:1}.settings .field__count{display:flex;justify-content:center;align-items:center;font-size:3rem}.settings .field__count .number{margin:0 3rem}.settings .field__count .arrow{height:4rem;cursor:pointer;-webkit-filter:brightness(80%);filter:brightness(80%)}.settings .field__count .arrow:hover{-webkit-filter:brightness(100%);filter:brightness(100%)}.settings .field__count .arrow--decrease{-webkit-animation:0.5s infinite alternate arrow-backwards;animation:0.5s infinite alternate arrow-backwards}.settings .field__count .arrow--increase{-webkit-animation:0.5s infinite alternate arrow-forwards;animation:0.5s infinite alternate arrow-forwards}.settings .btn-group{display:flex;justify-content:space-around;max-width:600px;width:100%}@-webkit-keyframes arrow-backwards{from{transform:translateX(0) rotate(90deg)}to{transform:translateX(-10px) rotate(90deg)}}@keyframes arrow-backwards{from{transform:translateX(0) rotate(90deg)}to{transform:translateX(-10px) rotate(90deg)}}@-webkit-keyframes arrow-forwards{from{transform:translateX(0) rotate(-90deg)}to{transform:translateX(10px) rotate(-90deg)}}@keyframes arrow-forwards{from{transform:translateX(0) rotate(-90deg)}to{transform:translateX(10px) rotate(-90deg)}}

.answer-response{display:flex;justify-content:center;align-items:center;width:100%;height:100%;position:fixed;font-size:4rem;opacity:1;background-color:rgba(0,0,0,0.3);-webkit-animation:1s forwards add-points;animation:1s forwards add-points;z-index:2}.answer-response--correct{color:green}.answer-response--fail{color:red}.play{width:100%;z-index:1}.play .round{display:flex;flex-direction:column;justify-content:space-around;align-items:center;height:100%}.play .round .btn-group{display:flex;justify-content:space-around;width:100%;max-width:400px}@-webkit-keyframes add-points{0%{font-size:4rem;opacity:1;z-index:2}70%{font-size:8rem;opacity:1;z-index:2}99%{font-size:8rem;opacity:0;z-index:2}100%{font-size:8rem;opacity:0;z-index:0}}@keyframes add-points{0%{font-size:4rem;opacity:1;z-index:2}70%{font-size:8rem;opacity:1;z-index:2}99%{font-size:8rem;opacity:0;z-index:2}100%{font-size:8rem;opacity:0;z-index:0}}

.spinner{display:flex;flex-direction:column;justify-content:center;align-items:center;height:100%;width:100%}.spinner__frame{width:200px}.spinner__frame .pic{width:100%;transform:scale(0.9);-webkit-animation:0.7s infinite alternate morty-face;animation:0.7s infinite alternate morty-face}.spinner__text{color:white;font-size:2.5rem;width:200px;margin:5rem auto 0 auto;padding-left:6rem;align-self:flex-start}.spinner__text:after{content:"LOADING";-webkit-animation:3s infinite loading;animation:3s infinite loading}@-webkit-keyframes morty-face{from{transform:scale(0.9)}to{transform:scale(1)}}@keyframes morty-face{from{transform:scale(0.9)}to{transform:scale(1)}}@-webkit-keyframes loading{0%{content:"LOADING"}25%{content:"LOADING."}50%{content:"LOADING.."}75%{content:"LOADING..."}}@keyframes loading{0%{content:"LOADING"}25%{content:"LOADING."}50%{content:"LOADING.."}75%{content:"LOADING..."}}

.character{display:flex;flex-direction:column;justify-content:space-around;width:300px;max-width:calc(100% - 80px);height:auto;max-height:calc(100vh - 150px)}.character__pic{width:100%;border-radius:10px 10px 0 0}.character__info{border-radius:0 0 10px 10px;background-color:#365c73;padding:1rem 0}.character__info .field{display:flex;padding:0.5rem 2rem;font-size:1.2rem;color:white;letter-spacing:2px}.character__info .field__title{font-weight:bolder;display:inline-block;margin:0 1rem 0 0}.character__info .field__data{margin:0}

.scoreboard{display:flex;justify-content:space-around;width:100%;height:50px;font-size:1.6rem;color:white}.scoreboard__item{text-align:center;margin:0}.scoreboard__streak{display:flex}.scoreboard__streak .streak-num{color:white}.scoreboard__streak .streak-num--upfive{color:orange;-webkit-animation:1.4s streak;animation:1.4s streak}.scoreboard__streak .streak-num--upten{color:red;-webkit-animation:1.4s streak;animation:1.4s streak}.scoreboard__streak .streak-logo{height:40px;margin-left:1rem}@-webkit-keyframes streak{0%{transform:scale(1)}50%{transform:scale(1.5)}100%{transform:scale(1)}}@keyframes streak{0%{transform:scale(1)}50%{transform:scale(1.5)}100%{transform:scale(1)}}

.resume{display:flex;flex-direction:column;justify-content:space-around;align-items:center;width:100%;height:100vh;color:white;font-size:3rem;overflow:hidden}.resume__title{font-size:4rem;text-align:center}.resume__pic{max-width:80%;max-height:50%}

.records{display:flex;flex-direction:column;align-items:center;justify-content:center;width:100%;color:white;margin-bottom:20px;overflow-y:hidden}.records__title{font-size:3.5rem}.records__scores{flex-grow:1;overflow-y:auto;text-align:center;width:100%;padding:0 1rem}.records__scores>.header{font-size:2rem;display:flex;font-weight:bold}.records__scores>.header>p{flex-grow:1}.records__scores .row:nth-child(even){background-color:rgba(255,255,255,0.2)}.records__scores .row:nth-child(odd){background-color:rgba(255,255,255,0.1)}.records .row{display:flex;width:100%;font-size:1.8rem}.records .row>p{flex-grow:1}

